* {
  margin: 0;
  padding: 0;
  font-family: Roboto Condensed;
}

.container {
  max-width: 768px;
  padding: 20px;
  margin: 0 auto;
}

@media (max-width: 575.98px) {
  .container {
    max-width: 550px;
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .container {
    max-width: 767px;
  }
}
