.gallery__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.gallery__image {
    max-width: 31%;
    margin-bottom: 20px;
    cursor: pointer;
    transition: all 0.2s ease;
}

.gallery__image img{
    max-width: 100%;
}

.gallery__image:hover {
    transform: scale(1.1);
}

@media (max-width: 575.98px) {
    .gallery__image {
        max-width: 100%;
        margin: 10px auto;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .gallery__content {
        justify-content: center;
    }

    .gallery__image {
        max-width: 49%;
        margin: 2px;
    }
}
