.popup-form button {
    background: #4997D0;
    width: 100%;
    padding: 8px 11px;
    border: 1px solid #4997D0;
    border-radius: 3px;
    color: #ffffff;
    transition: all 0.4s ease;
    cursor: pointer;
}

.popup-form button:hover {
    background: #fff;
    color: #4997D0;
}