.popup {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0, 0.7);
}

.popup-block {
    padding: 30px;
    background: #ffffff;
    max-width: 620px;
    margin: 0 auto;
    display: grid;
    position: relative;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: 58% 38%;
    grid-template-rows: 60% 40%;
}

.image-side {
    grid-column-start: 1;
}

.image-side img {
    max-width: 100%;
}

.comments-side {
    grid-column-start: 2;
    grid-row-start: 1;
    margin-left: 10px;
}

.popup-form {
    grid-column-start: 1;
    margin-top: 10px;
}

.close-btn {
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
    transition: all 0.2s ease;
    width: 20px;
    height: 20px;
}

.close-btn:hover {
    transform: scale(1.1);
}

.close-btn span {
    display: block;
    width: 25.46px;
    height: 0px;
    border: 1px solid #000000;
    transform: translate(-6px, 7px) rotate(-45deg);
}

.close-btn span:after {
    content: '';
    display: block;
    width: 25.46px;
    height: 0px;
    border: 1px solid #000000;
    transform: rotate(90deg);
}

.popup-form input {
    width: 100%;
    padding: 8px 11px;
    border: 1px solid #CCCCCC;
    box-sizing: border-box;
    border-radius: 3px;
    margin-bottom: 16px;
}

@media (max-width: 575.98px) {
    .popup {
        min-height: 100vh;
    }
    .popup .container {
        width: 100%;
        padding: 0;
    }

    .popup-block {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(10, 10vh);
        min-height: 100vh;
        padding: 0;
    }

    .image-side {
        grid-row-start: 1;
        grid-row-end: 5;
    }

    .image-side img {
        max-width: 100%;
    }

    .comments-side {
        grid-row-start: 5;
        grid-row-end: 9;
        grid-column-start: 1;
        overflow-y: scroll;
        max-height: 210px;
        padding: 10px;
        margin-left: 0;
    }

    .popup-form {
        grid-row-start: 8;
        grid-row-end: 10;
        grid-column-start: 1;
        justify-self: end;
        padding: 0 10px;
        margin-top: 0;
        position: fixed;
        bottom: 10px;
        background: rgba(255, 255, 255, 0);
    }

    .popup-form input {
        margin-bottom: 10px;
    }
}

